body {
	margin: 0;
}

.rec.rec-arrow {
	background-color: gray;
}

.rec.rec-arrow:focus-visible {
	background-color: black;
}

button.rec-dot {
	background-color: gray;
	box-shadow: black;
}

.background-shade-1 {
	background: rgb(0, 0, 0);
	background: linear-gradient(
		155deg,
		rgba(0, 0, 0, 1) 35%,
		rgba(122, 122, 122, 1) 83%
	);
}

.ticket-team-name {
	font-family: SF Pro Display;
	font-size: 32px;
	font-weight: 700;
	line-height: 38px;
	letter-spacing: -0.02em;
}

.ticket-team-member {
	font-family: SF Pro Display;
	font-size: 20px;
	font-weight: 700;
	line-height: 24px;
	letter-spacing: -0.02em;
}

.ticket-team-bg {
	background-image: url("../public/assets/teamcard/bg.svg");
	background-repeat: no-repeat;
	background-position: center;
}

.ticket-team-round {
	font-family: SF Pro Display;
	color: #969696;
	font-size: 24px;
	font-weight: 500;
	line-height: 29px;
	letter-spacing: -0.02em;
	text-align: center;
}

.ticket-team-date {
	font-family: SF Pro Display;
	font-size: 24px;
	font-weight: 700;
	line-height: 29px;
	letter-spacing: -0.02em;
	text-align: center;
}

.ticket-team-time {
	font-family: SF Pro Display;
	color: #969696;
	font-size: 24px;
	font-weight: 600;
	line-height: 29px;
	letter-spacing: -0.02em;
	text-align: center;
}

.team-hosted-by {
	font-family: SF Pro Display;
	font-size: 20px;
	font-weight: 700;
	line-height: 24px;
	letter-spacing: -0.02em;
	text-align: center;
}

.team-dashed-line {
	border: 2px dashed rgba(0, 0, 0, 0.19);
}

.team-number {
	font-family: SF Pro Display;
	font-size: 36px;
	font-weight: 800;
	writing-mode: vertical-lr;
	text-orientation: mixed;
	transform: rotate(-180deg);
}